.detail-box { min-height: 600px;
    display: grid; grid-template-columns: 30% 70%;
    div { padding: 20px; font-weight: bold;}
    img { width: 100%; border-radius: 5px; border: 5px solid #fff;}
    .dtit { font-size: 40px;}
    .dyear { font-size: 20px; }
    .dgenre {font-size: 20px; margin: 15px 0;
        li { display: inline-block; padding-right: 10px; }
    }
    .dsummary { font-weight: normal; font-size: 17px;}
}