.footer-wrap { padding: 30px 0; background: darkblue;
    text-align: center;
    ul { list-style: none;
        li { display: inline-block; padding: 0 5px;
        }
        li.aTag { color: #fff; cursor: pointer;
        }
        li.aTag:hover { color: gray;}
        li:before {
            content: '- ';
        }
        li:first-child:before { display: none;}
    }
    p {
        span {color: #fff; text-decoration: none;
            cursor: pointer;
        }
        span:hover {color: gray;}
    }
}